import Hammer from 'hammerjs'
export default {
  mounted() {
    let square = this.$refs.page;
    var hammer = new Hammer(square);
    hammer.on('swipeleft', (e) => {
      this.$router.push('/shoucang');
    });

    this.$store.commit('layout/setPageActive',0);
  },
  methods : {
    handleClick() {
      console.log('点击事件2');
    },
  },
}



